<template>
  <Demo-block title="Message " subtitle="" :README="README">
    <Demo-row title="" description="2 " :code="example1">
      <j-button class="demo-toast-btn" @click.stop="handleClick()"></j-button>
    </Demo-row>

    <Demo-row title="" description="" :code="example1">
      <j-button class="demo-toast-btn" @click.stop="handleClick('success')"></j-button>
      <j-button class="demo-toast-btn" @click.stop="handleClick('warn')"></j-button>
      <j-button class="demo-toast-btn" @click.stop="handleClick('info')"></j-button>
      <j-button class="demo-toast-btn" @click.stop="handleClick('error')"></j-button>
    </Demo-row>

    <Demo-row title="HTML" description="content HTML " :code="example1">
      <j-button class="demo-toast-btn" @click.stop="handleClick('info', 'html')"></j-button>
    </Demo-row>

    <Demo-row title="" description="Message" :code="example1">
      <j-button class="demo-toast-btn" @click.stop="handleClick('info', '', 64)"></j-button>
    </Demo-row>
  </Demo-block>
</template>

<script>
import DemoCommon from "../common/demo-common";
import README from "./README.md";

export default {
  mixins: [DemoCommon],
  data() {
    return {
      README
    };
  },
  computed: {
    example1() {
      return ` this.$message({
    type,
    content,
    verticalOffset,
    onClose() {
        console.log("close");
    }
});`
    }
  },
  methods: {
    handleClick(type = "info", text = "text", verticalOffset = 32) {
      const content =
        text !== "html" ? "" : `<p style="color: red;"></p>`;
      this.$message({
        type,
        content,
        verticalOffset,
        onClose() {
          console.log("close");
        }
      });
    }
  }
};
</script>
