<template>
  <Demo-block title="Toast " subtitle="" :README="README">
    <Demo-row title="" :code="example1">
      <j-button class="demo-toast-btn" name="normal" @click.stop="handleClick"></j-button>
      <j-button class="demo-toast-btn" name="normal-close" @click.stop="handleClick"></j-button>
      <j-button class="demo-toast-btn" name="normal-hide" @click.stop="handleClick"></j-button>
    </Demo-row>

    <Demo-row title="" :code="example2">
      <j-button class="demo-toast-btn" name="warn" @click.stop="handleClick"></j-button>
      <j-button class="demo-toast-btn" name="info" @click.stop="handleClick"></j-button>
      <j-button class="demo-toast-btn" name="error" @click.stop="handleClick"></j-button>
      <j-button class="demo-toast-btn" name="success" @click.stop="handleClick"></j-button>
    </Demo-row>

    <Demo-row title="" :code="example3">
      <j-button class="demo-toast-btn" name="left" @click.stop="handleClick"></j-button>
      <j-button class="demo-toast-btn" name="right" @click.stop="handleClick"></j-button>
    </Demo-row>

    <Demo-row title="" :code="example4">
      <j-button class="demo-toast-btn" name="callback" @click.stop="handleClick">toast</j-button>
    </Demo-row>
  </Demo-block>
</template>

<script>
import DemoCommon from "../common/demo-common";
import README from "./README.md";

export default {
  mixins: [DemoCommon],
  data() {
    return {
      README
    };
  },
  computed: {
    example1() {
      return `this.$toast({
    content: ""
});
this.$toast({
    content: "",
    autoClose: false
});
this.$toast({
    content: "",
    showClose: false
});`;
    },
    example2() {
      return `this.$toast({
    type,
    content
});`;
    },
    example3() {
      return `this.$toast({
    content,
    position
});`
    },
    example4() {
      return `this.$toast({
    ...
    onClose() {
        this.$message({
        content: "toast ",
        position: "top-left"
        });
    }
});`
    }
  },
  methods: {
    handleClick() {
      let selector = event.target.name;

      switch (selector) {
        case "normal":
          this.$toast({
            content: ""
          });
          break;
        case "normal-close":
          this.$toast({
            content: "",
            autoClose: false
          });
          break;
        case "normal-hide":
          this.$toast({
            content: "",
            showClose: false
          });
          break;
        case "warn":
        case "info":
        case "success":
        case "error":
          this.$toast({
            type: selector,
            content: `Content ${selector}`
          });
          break;
        case "left":
        case "right":
        case "center":
          this.$toast({
            content: `Content ${selector}`,
            position: `top-${selector}`
          });
          break;
        case "callback":
          this.$toast({
            content: `Content ${selector}`,
            position: `top-right`,
            onClose() {
              this.$message({
                content: "toast ",
                position: "top-left"
              });
            }
          });
          break;
        default:
          break;
      }
    }
  }
};
</script>
