<template>
  <Demo-block title="Tabs " subtitle="" :README="README">
    <Demo-row title="" description="" :code="example1">
      <j-tabs @change="handleChange">
        <j-tab label="Tab 1" id="tab1">Content of Tab Pane 1</j-tab>
        <j-tab label="Tab 2" id="tab2">Content of Tab Pane 2</j-tab>
        <j-tab label="Tab 3" id="tab3">Content of Tab Pane 3</j-tab>
        <j-tab label="Tab 4" id="tab4">Content of Tab Pane 4</j-tab>
      </j-tabs>
    </Demo-row>

    <Demo-row title="" description="" :code="example2">
      <j-tabs card>
        <j-tab label="Tab 1" id="tab1">Content of Tab Pane 1</j-tab>
        <j-tab label="Tab 2" id="tab2" closable>Content of Tab Pane 2</j-tab>
        <j-tab label="Tab 3" id="tab3">Content of Tab Pane 3</j-tab>
        <j-tab label="Tab 4" id="tab4">Content of Tab Pane 4</j-tab>
      </j-tabs>
    </Demo-row>

    <Demo-row title="" description="v-modeltab" :code="example3">
      <j-tabs v-model="tab">
        <j-tab label="Tab 1" id="tab1" cacheable>Content of Tab Pane 1</j-tab>
        <j-tab label="Tab 2" id="tab2">Content of Tab Pane 2</j-tab>
        <j-tab label="Tab 3" id="tab3">Content of Tab Pane 3</j-tab>
        <j-tab label="Tab 4" id="tab4">Content of Tab Pane 4</j-tab>
      </j-tabs>
    </Demo-row>

    <Demo-row title="" description="tab" :code="example4">
      <j-tabs v-model="otherTabActive">
        <j-tab label="Tab 1" id="tab1">Content of Tab Pane 1</j-tab>
        <j-tab label="Tab 2" id="tab2" disabled>Content of Tab Pane 2</j-tab>
        <j-tab label="Tab 3" id="tab3">Content of Tab Pane 3</j-tab>
        <j-tab label="Tab 4" id="tab4">Content of Tab Pane 4</j-tab>
      </j-tabs>
    </Demo-row>

    <Demo-row title="Tab" description="TabsTab">
      <j-button size="mini" @click.stop="handleClick('add')"></j-button>
      <j-button size="mini" @click.stop="handleClick('dec')"></j-button>

      <div class="row-item">
        <j-tabs v-model="anotherTabActive">
          <j-tab
            v-for="(item, index) in tabs"
            :label="`${item.label}+${index}`"
            :id="`${item.label}+${index}`"
            :key="index"
          >
            {{
            item.content + index
            }}
          </j-tab>
        </j-tabs>
      </div>
    </Demo-row>
  </Demo-block>
</template>

<script>
import DemoCommon from "../common/demo-common";
import README from "./README.md";

export default {
  mixins: [DemoCommon],
  data() {
    return {
      range: 10,
      tab: "tab1",
      tabActive: "tab3",
      otherTabActive: "tab1",
      anotherTabActive: "tab1",

      tabs: [
        {
          label: "tab",
          content: "tabContent"
        }
      ],

      UI: {
        tab: true
      },

      README
    };
  },
  computed: {
    example1() {
      return `<j-tabs @change="handleChange">
    <j-tab label="Tab 1" id="tab1">Content of Tab Pane 1</j-tab>
    <j-tab label="Tab 2" id="tab2">Content of Tab Pane 2</j-tab>
    <j-tab label="Tab 3" id="tab3">Content of Tab Pane 3</j-tab>
    <j-tab label="Tab 4" id="tab4">Content of Tab Pane 4</j-tab>
</j-tabs>`;
    },
    example2() {
      return `<j-tabs card>
    <j-tab label="Tab 1" id="tab1">Content of Tab Pane 1</j-tab>
    <j-tab label="Tab 2" id="tab2" closable>Content of Tab Pane 2</j-tab>
    <j-tab label="Tab 3" id="tab3">Content of Tab Pane 3</j-tab>
    <j-tab label="Tab 4" id="tab4">Content of Tab Pane 4</j-tab>
</j-tabs>`;
    },
    example3() {
      return `<j-tabs v-model="tab">
    <j-tab label="Tab 1" id="tab1" cacheable>Content of Tab Pane 1</j-tab>
    <j-tab label="Tab 2" id="tab2">Content of Tab Pane 2</j-tab>
    <j-tab label="Tab 3" id="tab3">Content of Tab Pane 3</j-tab>
    <j-tab label="Tab 4" id="tab4">Content of Tab Pane 4</j-tab>
</j-tabs>`;
    },
    example4() {
      return `<j-tabs v-model="otherTabActive">
    <j-tab label="Tab 1" id="tab1">Content of Tab Pane 1</j-tab>
    <j-tab label="Tab 2" id="tab2" disabled>Content of Tab Pane 2</j-tab>
    <j-tab label="Tab 3" id="tab3">Content of Tab Pane 3</j-tab>
    <j-tab label="Tab 4" id="tab4">Content of Tab Pane 4</j-tab>
</j-tabs>`;
    }
  },
  methods: {
    handleClick(type) {
      const handler = {
        add: () => this.tabs.push({ label: "tab", content: "tabContent" }),
        dec: () => {
          if (this.tabs.length) {
            this.tabs = this.tabs.slice(0, this.tabs.length - 1);
          }
        }
      };
      handler[type]();
    },
    handleChange() {
      this.$message({
        content: "tab"
      });
    }
  }
};
</script>
<style lang="scss">
.demo-row {
  .tabs-content {
    max-width: 640px;
    height: 100px;
    border: 1px solid #e4e7ed;
    border-top: none;
  }
  &:nth-of-type(2) {
    .tabs-content {
      height: 100px;
      border: none;
    }
  }
}
</style>
