<template>
  <Demo-block
    title="MessageBox "
    subtitle=""
    :README="README"
  >
    <Demo-row title="" description="" :code="example1">
      <j-button class="demo-message-btn"  @click.stop="handleClick('alert')"></j-button>
    </Demo-row>

    <Demo-row title="" description="" :code="example2">
      <j-button class="demo-message-btn" @click.stop="handleClick('custom-content')"></j-button>
    </Demo-row>

    <Demo-row title="" description="" :code="example3">
      <j-button class="demo-message-btn" @click.stop="handleClick('custom')"></j-button>
    </Demo-row>
  </Demo-block>
</template>

<script>
import DemoCommon from "../common/demo-common";
import README from "./README.md";

export default {
  mixins: [DemoCommon],
  data() {
    return {
      README
    };
  },
  computed: {
    example1() {
      return `this.$alert({
    title: "",
    content: "",
    onConfirm: () => this.$message({
        content: ""
    })
});`;
    },
    example2() {
      return `this.$confirm({
    title: "",
    content: "?",
    onConfirm: () => this.$message({
        content: ""
    }),
    onCancel: () => this.$message({
        type: "warn",
        content: ""
    })
});`
    },
    example3() {
      return `this.$confirm({
    title: "",
    content: "",
    confirmButtonText: "",
    cancelButtonText: "",
    onConfirm: () => this.$message({
        content: ""
    }),
    onCancel: () => this.$message({
        type: "warn",
        content: ""
    })
});`;
    }
  },
  methods: {
    handleClick(selector) {
      const _self = this;
      switch (selector) {
        case "alert":
          this.$alert({
            title: "",
            content: "",
            onConfirm: () =>
              this.$message({
                content: ""
              })
          });
          break;
        case "confirm":
          this.$confirm({
            title: "",
            content: "?",
            onConfirm: () =>
              this.$message({
                content: ""
              }),
            onCancel: () =>
              this.$message({
                type: "warn",
                content: ""
              })
          });
          break;
        case "custom-content":
          this.$confirm({
            title: "",
            content: `<h1 style="color: red;">HTML</h1>`,
            onConfirm: () =>
              this.$message({
                content: ""
              }),
            onCancel: () =>
              this.$message({
                type: "warn",
                content: ""
              })
          });
          break;
        case "custom":
          this.$confirm({
            title: "",
            content: "",
            confirmButtonText: "",
            cancelButtonText: "",
            onConfirm: () =>
              this.$message({
                content: ""
              }),
            onCancel: () =>
              this.$message({
                type: "warn",
                content: ""
              })
          });
          break;
      }
    }
  }
};
</script>
